@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;700&display=swap);

body{
    font-family: 'Roboto', sans-serif;
}
.card--background{
    width: 100%;
    background-repeat: no-repeat;
    border-radius: 10px;
    margin-bottom: 40px;
}
.color--overlay{
    width: 100%;
    border-radius: 10px;
    
}
.color--overlay__compare{
    border-radius: 10px;
    border: 1px solid grey;
    margin-bottom: 20px;
}
@media only screen and (max-width: 600px) {
    body {
      background-color: lightblue;
    }
  }
.card--wrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
} 

.main--weather{
    display: flex;
    align-items: center;
    color: rgb(95, 95, 95);
}
.temp{
    font-size: 95px;
    color: white;
    margin: 0;
    padding-left: 20px;
    margin-bottom: 20px;
}
.secondary--forcast{
    color: rgb(95, 95, 95);
    padding-right: 20px;
}
.city--name{
    color: rgb(95, 95, 95);
    text-align: center;
    margin: 0;
    padding-top: 20px;
    padding-bottom: 10px;
}
.temp--small{
    color: rgb(95, 95, 95);
    font-size: 34px;
}
.card--wrapper__compare{
    color: rgb(95, 95, 95);
    font-size: 16px;
    padding-left: 20px; 
}
.compare--wrapper__big{
    position: relative;
    margin-bottom: 20px;
}
.close--button{
    position: absolute;
    top: -10px;
    right: -10px;
    border-radius: 50px;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px 8px;
    border: 1px solid grey;
    cursor: pointer;
}
.close--button:hover{
    opacity: 0.7;
    transition: 0.2s;
}
.form{
    height: 20px;
   
}
.form--button{
    background-color: lightskyblue;
    border-radius: 20px;
    padding: 5px 10px;
    border: 1px solid lightslategray;
    margin-left: 10px;
    cursor: pointer;
}
.form--button:hover{
    opacity: 0.7;
    transition: 0.2s;
}
.form--wrapper{
    text-align: center;
    margin-bottom: 20px;
}
.input{
    height: 20px;
    background-color: lightsteelblue;
    border: 1px solid lightslategrey;

}
